.apply-example {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  border-radius: 1.5rem;
  --tw-bg-opacity: 1;
  background-color: rgba(245, 248, 255, var(--tw-bg-opacity));
}

@media (min-width: 768px) {
  .apply-example {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    border-radius: 34px;
  }
}

